@mixin flex-center($direction,$justifyContent,$alignItems,$gap:0) {
    display: flex;
    flex-direction: $direction;
    justify-content: $justifyContent;
    align-items: $alignItems;
    gap:$gap;
  }
@mixin global-reset(){
  *,
  *::after,
  *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font-family) !important;
    unicode-bidi: plaintext;
    text-align: start;    
    -moz-box-sizing: border-box;

    
  
  }
}
@mixin grid-cell($gap:10px,$cells:3){
  display: grid;
  grid-template-columns: repeat($cells, 1fr);
  grid-gap: $gap;
  grid-auto-flow: row;
  direction: rtl;
}